@import "variables";
@import "mixins";
@import "fonts";
@import "global";
@import "animation";

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

*:focus:not(:focus-visible) {
  outline: none;
}

:root {
  --offset-page: 30px;
  --header-height: 84px;

  @media (max-width: $viewport-m) {
    --header-height: 68px;
  }

  @media (max-width: $viewport-s) {
    --offset-page: 16px;
    --header-height: 56px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  max-width: calc(1140px + var(--offset-page) * 2);
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--offset-page);
}

.content-img {
  height: 100%;
  object-fit: cover;
  object-position: 50% 0;
  //background-color: $bg-img;
}

.content {
  position: relative;
  max-width: 100%;
  transition: max-width $delay-default;
}

.btn {
  padding: 16px 32px;
  flex-shrink: 0;
  font-weight: 600;
  color: white;
  border: 2px solid $color-blue;

  @include anim-shutter("out");

  @media (max-width: $viewport-m) {
    padding: 10px 22px;
  }

  @media (max-width: $viewport-m) {
    width: 100%;
    max-width: 350px;
    margin: 0 auto;
  }
}
