.burger-menu {
  --font-size: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  //height: 100vh;
  transform: translateY(-100px);
  max-height: 0;
  display: flex;
  background: $color-graphite;
  visibility: hidden;
  transition-property: transform, max-height, visibility;
  transition-duration: .6s;
  transition-timing-function: $timing-default;
  overflow: hidden;
  z-index: 2;

  @media (max-width: 1100px) {
    --font-size: 18px;
  }

  @media (max-width: $viewport-m) {
    display: none;
  }
}

.nav-menu-active .burger-menu {
  max-height: 150vh;
  transform: translateY(0);
  visibility: visible;
}

.burger-menu__wrap {
  display: flex;
}

.burger-menu__nav,
.burger-menu__content {
  padding: 202px 0 64px 0;
}

.burger-menu__nav {
  max-width: 320px;
  flex-grow: 1;
  font-weight: 600;
  font-size: calc(var(--font-size) + 8px);

  .social-icons {
    width: 180px;
  }
}

.burger-menu__list {
  position: relative;
  max-width: 319px;
  margin-bottom: 108px;
  padding-right: 25%;
  display: grid;
  gap: 48px;

  &::after {
    content: "";
    position: absolute;
    top: 8px;
    right: -13px;
    width: 24px;
    height: 24px;
    border-left: 1px solid #FFF;
    border-bottom: 1px solid #FFF;
    transform: rotate(45deg);
    background: $color-graphite;

    @media (max-width: $viewport-m) {
      content: none;
    }
  }
}

.burger-menu__link {
  color: $color-menu-item;
  transition: color $delay-default;

  &:hover,
  &:focus,
  &.active {
    color: white;
  }
}

.content-menu {
  display: flex;
  padding-left: 69px;
  border-left: 1px solid $color-gray;

  @media (max-width: $viewport-m) {
    display: none;
  }
}

.content-menu__left {
  margin-right: 56px;
}

.content-menu__list {
  display: flex;
  max-width: 293px;
  flex-direction: column;

  dd + dt {
    margin-top: 56px;
  }
}

.content-menu__term {
  margin-bottom: 32px;
  font-weight: 600;
  font-size: calc(var(--font-size) + 4px);
  line-height: 1.3;
}

.content-menu__def {
  font-size: var(--font-size);
  text-transform: uppercase;

  p + p {
    margin-top: 20px;
  }
}
