@mixin anim-shutter($par) {
  position: relative;
  z-index: 0;
  //transition: color $delay-default;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background-color: $color-blue;
    transform: scale3d(0, 1, 1);
    transition-property: background-color, transform;
    transition-duration: $delay-default;
    transition-timing-function: $timing-default;

    @if ($par == "in") {
      transform: scale3d(1, 1, 1);
    }
  }

  &:hover,
  &:focus {
/*    color: white;

    @if ($par == "in") {
      color: $color-blue;
    }*/

    &::before {
      transform: scale3d(1, 1, 1);

      @if ($par == "in") {
        transform: scale3d(0, 1, 1);
      }
    }
  }

  &:active {
    transform: scale3d(.99, .99, 1);
  }
}
