.why {
  padding-top: 160px;
  padding-bottom: 160px;

  @media (max-width: $viewport-m) {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media (max-width: $viewport-s) {
    padding-top: 72px;
    padding-bottom: 72px;
  }

  h2 {
    margin-bottom: 32px;
    padding-left: 24px;
    font-weight: 600;
    font-size: 40px;
    line-height: 1.3;
    border-left: 4px solid $color-blue;
    color: $font-black;

    @media (max-width: $viewport-s) {
      margin-bottom: 24px;
      font-size: 28px;
      padding-left: 0;
      border-left: none;
    }
  }
}

.why__list {
  max-width: 750px;
  margin-left: 28px;
  font-size: 20px;
  color: $font-light-black;

  @media (max-width: $viewport-s) {
    margin-left: 0;
    font-size: 18px;
  }

  li + li {
    margin-top: 20px;
  }
}
