.banner {
  position: relative;
  min-height: calc(100vh - var(--header-height, 84px));
  color: white;
  background: linear-gradient(90deg, $bg-black calc(192px + 50vw), $color-graphite calc(192px + 50vw));
  overflow: hidden;

  @media (max-width: $viewport-m) {
    min-height: initial;
    padding-bottom: 50px;
    background: linear-gradient(90deg, $bg-black 66%, $color-graphite 66%);
  }

  @media (max-width: $viewport-s) {
    background: $bg-black;
    overflow: initial;
  }
}

.banner__decor-text {
  position: absolute;
  top: -70px;
  left: calc(186px + 50vw);
  font-weight: bold;
  font-size: 180px;
  line-height: 1;
  color: #252930;
  opacity: .4;

  span {
    opacity: 0;
  }

  @media (max-width: $viewport-m) {
    left: 65%;
  }

  @media (max-width: $viewport-s) {
    display: none;
  }
}

.banner__inner {
  display: flex;

  @media (max-width: $viewport-s) {
    flex-direction: column;
  }
}

.banner__content {
  width: 40.35%;
  flex-shrink: 0;
  padding-top: calc((626 * 100% / 1140) - 400px);

  @media (max-width: $viewport-m) {
    width: 44%;
    padding-top: calc((100 * 100% / 1140));
  }

  @media (max-width: $viewport-s) {
    width: 100%;
    padding-top: 0;
  }

  p {
    color: $color-gray;
  }

  strong {
    display: block;
    margin: 10px 0 16px 0;
    font-size: Max(Min(72px, 6vw), 48px);
    line-height: 1;
    letter-spacing: .02em;

    @media (max-width: 1060px) {
      font-size: Max(Min(72px, 5.9vw), 48px);
    }

    @media (max-width: $viewport-s) {
      font-size: Min(48px, 13vw);
    }
  }

  @media (max-width: $viewport-m) {
    .social-icons__list {
      grid-template-columns: repeat(auto-fit, 30px);
    }
  }

  @media (max-width: $viewport-s) {
    .social-icons__list {
      display: none;
    }
  }
}

.banner__content-title {
  font-size: 18px;
  font-weight: 600;

  @media (max-width: $viewport-s) {
    font-size: 14px;
  }
}

.banner__wrap-img {
  width: 70.18%;
  flex-shrink: 0;
  margin-left: -8.77%;

  @media (max-width: $viewport-m) {
    width: 65%;
    margin-left: -9.5%;
  }

  @media (max-width: $viewport-s) {
    order: -1;
    width: 100%;
    margin: 0 0 20px 0;
  }
}

.banner__more {
  @include anim-shutter("in");
  margin: 40px 0 118px 0;
  text-transform: uppercase;

  @media (max-width: $viewport-l) {
    margin: 40px 0 8.2vw 0;
  }

  @media (max-width: $viewport-m) {
    margin-bottom: 7vw;
  }

  @media (max-width: $viewport-s) {
    margin-bottom: 72px;
  }
}
