.header {
  position: relative;
  display: flex;
  min-height: var(--header-height);
  color: white;
  z-index: 1;
}

.header__wrap {
  position: relative;
  width: 100%;
  display: flex;
  background: $color-graphite;
  border-bottom: 1px solid rgba(255, 255, 255, .1);
  z-index: 3;

  @media (max-width: $viewport-m) {
    border-bottom: 1px solid rgba(255, 255, 255, 0);
  }
}

.header__inner {
  display: flex;
  width: 100%;
  max-width: 1320px;
  align-items: center;
  margin: 0 auto;
  padding: 0 var(--offset-page);

  @media (max-width: $viewport-m) {
    align-items: stretch;
    padding: 0;
  }
}

.header__location {
  display: none;

  @media (max-width: $viewport-m) {
    display: block;
    padding: 0;
    width: 24px;
    height: 24px;
    color: $color-blue;
    margin-right: 16px;
  }
}

.header__menu-toggle {
  position: relative;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  width: 18px;
  height: 16px;
  padding: 0;

  &::after {
    content: "";
    position: absolute;
    width: 40px;
    height: 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.header__burger {
  width: 100%;
  height: 2px;
  background-color: currentColor;
  transition: width .15s;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transition: transform .3s;
    will-change: transform;
  }

  &::after {
    top: initial;
    bottom: 0;
  }
}

// Меняет бергер на крестик
.nav-menu-active .header__burger {
  width: 0;
}

.nav-menu-active .header__burger::before {
  transform: translateY(7px) rotate(-45deg);
}

.nav-menu-active .header__burger::after {
  transform: translateY(-7px) rotate(45deg);
}

.header__logo {
  width: 164px;
  height: 24px;
  margin-left: 16px;
  flex-shrink: 0;
  color: white;
  transition: color $delay-default;

  @media (max-width: 1100px) {
    width: 124px;
    height: 18px;
  }

  @media (max-width: $viewport-m) {
    margin-left: 0;
    order: -1;
    margin-right: auto;
  }

  @media (max-width: $viewport-s) {
    width: 110px;
    height: 16px;
  }

  &:hover,
  &:focus {
    color: $color-blue;
  }
}

.header__left {
  display: flex;
  align-items: center;

  @media (max-width: $viewport-m) {
    position: relative;
    padding: 0 var(--offset-page);
    flex-grow: 1;
    align-items: center;
    z-index: 1;

    @media (max-width: $viewport-m) {
      border-bottom: 1px solid rgba(255, 255, 255, .1);
    }
  }
}

.header__right {
  flex-grow: 1;
  display: flex;
  align-items: center;
  margin-left: 48px;

  @media (max-width: 1100px) {
    margin-left: 38px;
  }

  @media (max-width: $viewport-m) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    max-height: 0;
    margin-left: 0;
    padding: 0 var(--offset-page) 44px;
    flex-direction: column;
    align-items: flex-start;
    background-color: $color-graphite;
    transform: translateY(-50px);
    overflow: hidden;
    z-index: 0;
    visibility: hidden;
    transition-property: transform, max-height, visibility;
    transition-duration: .6s;
    transition-timing-function: $timing-default;
  }

  .social-icons {
    display: none;

    @media (max-width: $viewport-m) {
      display: block;
      margin-bottom: 32px;
    }
  }
}

.nav-menu-active .header__right {
  @media (max-width: $viewport-m) {
    max-height: 100vh;
    transform: translateY(0);
    visibility: visible;
  }
}

.header__nav {
  @media (max-width: $viewport-m) {
    margin-top: 100px;
  }
}

.header__nav-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-right: 30px;
  transition: opacity .6s;

  @media (max-width: $viewport-m) {
    flex-direction: column;
    align-items: flex-start;
    font-size: 18px;
  }
}

.nav-menu-active .header__nav-list {
  @media (min-width: $viewport-m + 1px) {
    opacity: 0;
  }
}

.header__nav-item {
  line-height: 2;

  &:not(:last-child) {
    margin-right: 32px;

    @media (max-width: $viewport-m) {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
}

.header__nav-link,
.header__phone {
  transition: color $delay-default;

  &:hover,
  &:focus {
    color: $color-blue;
  }
}

.header__phone {
  margin: 0 24px 0 auto;
  font-weight: 600;

  @media (max-width: $viewport-m) {
    margin: auto 0 12px 0;
  }
}

.header__email {
  display: none;

  @media (max-width: $viewport-m) {
    display: block;
    margin-bottom: 32px;
  }
}

.header__btn-map {
  @include anim-shutter("out");

  @media (max-width: 1100px) {
    padding: 10px 22px;
  }
}
